// Utility Bar Variables:
// --sb-utility-bar-background - background color
// --sb-utility-bar-text-color - text color

.sb-utility-bar {
  align-items: center;
  background-color: var(--sb-utility-bar-background);
  color: var(--sb-utility-bar-text-color);
  display: grid;
  grid-template-areas: 'left center right';
  grid-template-columns: auto;
  padding: 0.25rem 0.5rem;
}

.sb-utility-bar__left-region {
  grid-area: left;
  justify-self: flex-start;
}

.sb-utility-bar__center-region {
  grid-area: center;
  justify-self: center;
}

.sb-utility-bar__right-region {
  display: flex;
  grid-area: right;
  justify-self: end;
}

.sb-utility-bar-light {
  --sb-utility-bar-background: var(--sbt-theme-color-30);
  --sb-utility-bar-text-color: var(--sbt-theme-color-100);
}

.sb-utility-bar-dark {
  --sb-utility-bar-background: var(--sbt-theme-color-80);
  --sb-utility-bar-text-color: var(--sbt-theme-color-10);
}

.sb-utility-bar-transparent {
  --sb-utility-bar-background: transparent;
  --sb-utility-bar-text-color: var(--sbt-theme-color-100);
}
